<template>
  <div>
    <mt-header fixed title="监管记录详情">
      <router-link to="/regulatoryRecord" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="header">
      <div class="mui-card">
        <div class="mui-card-content">
          <div class="mui-card-content-inner">
            <ul>
              <li>
                日期<span>{{ data.createTime }}</span>
              </li>
              <li>
                乡镇<span>{{ townShip }}</span>
              </li>
              <li>
                受检单位<span>{{ companyName }}</span>
              </li>
              <li style="border: none">
                检查人<span>{{ data.inspector }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>

      <div class="mui-card">
        <div class="mui-card-header">检查内容与结果</div>
        <div class="mui-card-content">
          <div class="mui-card-content-inner">
            <p class="Title">
              生产记录（种养殖档案)
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.5rem"
              >
                <label>有</label>
                <input
                  type="radio"
                  name="production"
                  :checked="supervisionInfo.production == 1"
                />
              </span>
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:8rem; margin-top:-0.5rem"
              >
                <label>真实完整</label>
                <input
                  type="radio"
                  name="production"
                  :checked="supervisionInfo.production == 2"
                />
              </span>
            </p>

            <p class="Title">
              农产品产投入品记录（进、销、使用）
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.5rem"
              >
                <label>有</label>
                <input
                  type="radio"
                  name="inspection"
                  :checked="supervisionInfo.inspection == 1"
                />
              </span>
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:8rem; margin-top:-0.5rem"
              >
                <label>真实完整</label>
                <input
                  type="radio"
                  name="inspection"
                  :checked="supervisionInfo.inspection == 2"
                />
              </span>
            </p>

            <p class="Title">
              禁用投入品及滥用添加剂行为
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.5rem"
              >
                <label>有</label>
                <input type="radio" :checked="supervisionInfo.input == 1" name="input"/>
              </span>
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:8rem; margin-top:-0.5rem"
              >
                <label>真实完整</label>
                <input type="radio" :checked="supervisionInfo.input == 2" name="input"/>
              </span>
            </p>

            <p class="Title">
              农产品产地准出证明（销售记录）
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.5rem"
              >
                <label>有</label>
                <input type="radio" :checked="supervisionInfo.abuse == 1" name="abuse"/>
              </span>
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:8rem; margin-top:-0.5rem"
              >
                <label>真实完整</label>
                <input type="radio" :checked="supervisionInfo.abuse == 2" name="abuse"/>
              </span>
            </p>

            <p class="Title">
              检验记录
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.6rem"
              >
                <label>有</label>
                <input type="radio" :checked="supervisionInfo.origin == 1" name="origin"/>
              </span>
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:8rem; margin-top:-0.5rem"
              >
                <label>真实完整</label>
                <input type="radio" :checked="supervisionInfo.origin == 2" name="origin"/>
              </span>
            </p>

            <p class="Title">
              “三品一标使用”
              <span
                class="mui-input-row mui-radio mui-left"
                style="margin-left:6rem;margin-top:-0.5rem"
              >
                <label>合规</label>
                <input type="radio" :checked="supervisionInfo.standard == 1"/>
              </span>
            </p>
            <p style="font-size: 1rem;font-weight:500;color: black">其他</p>
            <div class="mui-input-row input">
              <input type="text" placeholder="水稻种植情况检查" />
            </div>
            <p style="font-size: 1rem;font-weight:500;color: black">
              现场发现的其他问题
            </p>
            <div class="mui-input-row input">
              <input type="text" v-model="data.otherProblems" />
            </div>
            <p style="margin-left: 1rem;margin-top:2rem">
              结论&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;不合格
            </p>
            <div class="mt-field">
              <mt-field
                v-model="conclusionData.order"
                label="责令修改"
              ></mt-field>
              <mt-field
                v-model="conclusionData.suggestion"
                label="建议处罚"
              ></mt-field>
              <mt-field
                v-model="conclusionData.others"
                label="其他处理"
              ></mt-field>
            </div>
            <div class="scene">
              <!--<span class="scenetitle">-->
              <!--现场图片-->
              <!--<img-->
              <!--class="live_photo"-->
              <!--:src="downloadUrl + data.scenePhotos"-->
              <!--v-if="data.scenePhotos"/>-->
              <!--</span>-->
              <!--<p style="margin-top: 8rem;margin-left: 2rem">签名<img class="sign_photo" :src="downloadUrl + data.sign" v-if="data.sign" /></p>-->
              <table class="image-upload-table">
                <tbody>
                  <tr>
                    <td width="40%">现场图片</td>
                    <td>
                      <div class="image-container">
                        <img
                          class="live_photo"
                          :src="downloadUrl + data.scenePhotos"
                          v-if="data.scenePhotos"
                        />
                      </div>
                      <a
                        v-if="data.scenePhotos"
                        style=" display: table"
                        @click="downloadFile_Live()"
                        >{{ data.scenePhotos.replace("/uploads/", "") }}</a
                      >
                    </td>
                  </tr>
                  <tr>
                    <td width="30%">签名</td>
                    <td>
                      <div class="image-container">
                        <img
                          class="sign_photo"
                          :src="downloadUrl + data.sign"
                          v-if="data.sign"
                        />
                      </div>
                      <a
                        style=" display: table"
                        v-if="data.sign"
                        @click="downloadFile_Sign()"
                        >{{ data.sign.replace("/uploads/", "") }}</a
                      >
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="supervisor">
              <p style="color:#000; padding:2px 5px 0 5px">GPS定位</p>
              <baidu-map class="map" :center="center" :zoom="zoom" ak="xaM7HHYaG0KjyOcouj09IWHavrggGUH5" @ready="handler">
          　　　<bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
                <bm-marker :position="center" :dragging="true" animation="BMAP_ANIMATION_BOUNCE">
                  <bm-label :content="content" :labelStyle="{color: 'red', fontSize : '15px'}" :offset="{width: -35, height: 30}"/>
                </bm-marker>
          　　</baidu-map>
            </div>
          </div>
        </div>
      </div>
      <div style="margin-left: 1rem"></div>
    </div>
  </div>
</template>

<script>
import Request from "@/configs/request.js";
import { Urls } from "@/configs/constants";
import BaiduMap from 'vue-baidu-map/components/Map/Map.vue';
import BmGeolocation from 'vue-baidu-map/components/controls/Geolocation.vue';
import BmMarker from 'vue-baidu-map/components/overlays/Marker';
import BmLabel from 'vue-baidu-map/components/overlays/Label';
import axios from "axios";
export default {
  name: "RegulatoryRecordDetails",
  components: {
　　BaiduMap,
　　BmGeolocation,
   BmMarker,
   BmLabel
　},
  data() {
    return {
      content:"",
      center: {lng: 0, lat: 0},  
      zoom: 1,
      id: 0,
      townShip: "",
      companyName: "",
      imageUrl: "",
      data: {},
      conclusionData: {},
      supervisionInfo: {},
      listLoading: true,
      downloadUrl: ""
    };
  },
  methods: {
    //监管记录详情
    getData() {
      Request()
        .get("/api/supervision_record/get/" + this.id, {
          id: this.id
        })
        .then(response => {
          this.data = response;
          this.conclusionData = JSON.parse(this.data.conclusionFalseInfo);
          this.supervisionInfo = JSON.parse(this.data.supervisionInfo);
        })
        .catch(error => {
          console.log(error);
        });
    },
    //下载
    downloadFile_Live() {
      axios({
        url: this.downloadUrl + this.data.scenePhotos,
        method: "GET",
        responseType: "blob" // important
      }).then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute(
          "download",
          this.data.scenePhotos.replace("/uploads/", "")
        ); //or any other extension
        document.body.appendChild(link);
        link.click();
        link.remove();
      });
    },
    downloadFile_Sign() {
      axios({
        url: this.downloadUrl + this.data.sign,
        method: "GET",
        responseType: "blob" // important
      }).then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", this.data.sign.replace("/uploads/", "")); //or any other extension
        document.body.appendChild(link);
        link.click();
        link.remove();
      });
    },
    handler ({BMap, map}) {
      this.center.lng = this.data.longitude ? this.data.longitude : 116.404
      this.center.lat = this.data.latitude ? this.data.latitude : 39.915
      this.zoom = 15
    }
  },
  created() {
    this.id = this.$route.query.id;
    this.townShip = this.$route.query.town;
    this.companyName = this.$route.query.company;
    this.downloadUrl = Urls.DOWNLOAD_URL();
    this.getData();
  }
};
</script>

<style scoped>
ul {
  margin-left: -3rem;
  font-weight: 800;
}

li {
  position: relative;
  line-height: 4rem;
  padding-left: 1rem;
  border-bottom: 0.03rem black solid;
}

span {
  position: absolute;
  left: 5rem;
  font-weight: 500;
}
.mui-radio input[type=radio] {
  top:6px!important
}
.mui-radio input[type=radio]:before {
  font-size: 15px!important;
}

.mui-card-content-inner {
  margin-right: -1rem;
  padding:5px!important;
}

.Title {
  font-size: 0.8rem;
  margin-top: 1rem;
  letter-spacing: -1.5px;
}

.scene {
  height: 15.5rem;
  border: 0.01rem black solid;
  margin-top: 1rem;
  margin-right: 1.5rem;
  margin-bottom: 1rem;
  padding: 0.5rem 0 0 2rem;
}

.scene span {
  left: 3rem;
  line-height: 7rem;
}

.supervisor {
  height: 185px;
  widows: 100%;
  border: 0.01rem black solid;
  margin-bottom: 1rem;
  margin-right: 1.5rem;
}

.supervisor span {
  left: 3rem;
  line-height: 8rem;
}
.mt-field {
  margin-right: 1rem;
}
.input {
  margin-right: 1.5rem;
}

.image-container {
  max-width: 110px;
  width: 100%;
  height: 80px;
  margin: 5px 0;
  border-radius: 5px;
  text-align: center;
  box-shadow: 1px 2px 5px 1px rgba(10, 8, 10, 0.61);
}
.image-container p {
  margin-top: 20px;
}
img {
  max-width: 100%;
  height: 100%;
}
.mui-radio.mui-left label {
  padding-right: 15px;
  padding-left: 37px!important;
}
.map {
  width: 98%;
  margin: 0 auto;
  height: 150px;
}
</style>



